article {
  height:auto;
  padding: 7% 0;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 300ms ease;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  text-decoration: none;
  color: black;
  transition: all 300ms ease;
  overflow: hidden;

  &::after {
    content: "";
    clear: both;
    display: table;
  }

  img {
    width: auto;
    height: 100px;
    bottom: -20px;
    right: 20px;
    position: absolute;
    box-shadow: 0 0 15px 0 rgba(0,0,0,0.2);
    transition: transform 300ms ease;
  }


  @media screen and (min-width: 2250px) {
    flex-basis: 17%;

    &:hover {
      flex-basis: 17%;
    }
  }

  @media screen and (min-width: 1920px) {
    flex-basis: 22%;


    &:hover {
      flex-basis: 22%;
    }
  }

  @media screen and (max-width: 1200px) {
    flex-basis: 43%;
    width: 43%;


    &:hover {
      flex-basis: 43%;
    }
  }

  @media screen and (max-width: 800px) {
    flex-basis: 100%;
  }

  // .logged-in {
  //   display: none;
  // }

  &.editable {
    flex: 9 2 13%;

    .website-wrapper {
      pointer-events: none;
    }

    input:not([type="color"]),
    textarea {
      border: 1px solid paleturquoise;
      margin: 5px;
      padding: 4px 10px;
    }

    .controls {
      transform: translateY(-50%) translateX(0px);
      opacity: 1;
    }

    .text-collapsed {
      max-height: 100% !important;
      overflow: visible !important;
    }
  }


  &:hover {
    // flex: 9 2 17.5%;
    padding:12% 0;

    .controls {
      transform: translateY(-50%) translateX(0px);
      opacity: 1;
    }

    .text-collapsed {
      max-height: 100% !important;
      overflow: visible !important;
    }

    .link {
      opacity: 1;
    }

    .category {
      letter-spacing: 3px;
    }

    img {
      transform: translateY(-20px);
    }
  }


  &.white {

    input,
    textarea,
    div {
      color: white;
    }
  }


  &:after {
    content: 'â†’';
    position: absolute;
    top: 5px;
    z-index: 99;
    right: 10px;
    transform: rotate(-45deg);
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    color: black;
    opacity: 0;
    font-weight: bold;
    // text-shadow: 1px 1px 0 white;
    font-size: 20px;
    transition: all 300ms ease;
  }
}

.website-wrapper {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
  z-index: 3;

  &::before {
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: all 500ms ease;
    background: rgba(0, 0, 0, 0.2);
    opacity: 0;
    z-index: 0;
  }
}

.text-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 10px 25px;
  z-index: 2;

  .text-shown {
    display: flex;
    align-items: flex-start;
    flex-direction: column;

    input {
      width: 100%;
    }
  }

  .text-collapsed {
    max-height: 0;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    width: 100%;
    transition: all 300ms ease;
  }

  .text-above {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  input[type='text'],
  textarea {
    background: none;
    border: none;
    outline: none;
    border-radius: 5px;
    padding: 5px;
    transition: all 300ms ease;
    border: 1px solid transparent;
    font-family: 'Barlow', sans-serif;
    // text-shadow:0 0 5px rgba(0, 0, 0, 0.3);
  }

  .title {
    font-size: 40px;
    font-weight: 400;
    margin-top: 0.5rem;
  }

  .link {
    font-size: 14px;
    font-weight: 700;
    text-align: right;
    opacity: 0;
    transition: all 300ms ease;
  }

  .desc {
    font-size: 16px;
    resize: none;
    width: 50%;
    font-weight: 400;
    margin-top: 0.5rem;
    line-height: 120%;
  }

  .category {
    font-size: 14px;
    letter-spacing: 1px;
    font-weight: 600;
    text-transform: uppercase;
    transition: letter-spacing 300ms ease;
  }

  input[type='color'] {
    background: none;
    width: 40px;
    padding: 0;
    margin: 0;
    outline: none;
    border: none;
    height: 25px;
    padding: 0 5px;
    border-radius: 5px;
    background: white;
  }
}

.controls {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateX(20px);
  right: -16px;
  z-index: 12;
  transition: all 500ms ease;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  button {
    border: none;
    cursor: pointer;
    padding: 5px 10px;
    background: white;
    color: #00446e;
    font-family: "Work Sans" !important;
    transition: all 300ms ease;
    border-radius: 10px;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);

    &:not(:first-child) {
      margin-top: 10px;
    }
  }
}

@keyframes spin {
  0% {

    top: 5px;
    right: 10px;
  }

  50% {
    top: -11px;
    right: -6px;
  }

  100% {

    top: 5px;
    right: 10px;
  }
}